import React from "react";
/* axios 인스톨후 임폴트 */
import axios from "axios";
/* Movie.js임폴트 */
import Movie from "./Movie";
import "./App.css";

class App extends React.Component {
	state = {
		isLoading: true,
		movies: [],
	};
	/* async 함수는 await를 쓰기위해 넣어줌 작업이 끝나기를 잠시 기다려주는 함수 */
	getMovies = async () => {
		/* api를 받아서 그안에서 무비 데이터를 뽑음 */
		const {
			data: {
				data: { movies },
			},
		} = await axios.get("https://yts-proxy.now.sh/list_movies.json");
		/*{ movies : movies }로 해야하는데 { movies }로 줄여서 씀*/
		this.setState({ movies, isLoading: false });
	};
	/* render() 시작전 먼저 시작되는 함수 */
	componentDidMount() {
		this.getMovies();
	}

	/* 무비api를 받아서 준비될동안 로딩 보여주고 그후 무비 데이터 받아서 화면에 뿌려줌 */
	render() {
		const { isLoading, movies } = this.state;
		/* 3항 연산 isLoading이 참이면 Loading... */
		/* 기본식에서 HTML식으로 변형을 줌 */
		return (
			<section className='container'>
				{isLoading ? (
					<div className='loader'>
						<span className='loader__text'>Loading...</span>
					</div>
				) : (
					<div className='movies'>
						{movies.map((movie) => {
							/* 무비에서 필요한 변수들을 불러옴 */
							return (
								<Movie
									key={movie.id}
									id={movie.id}
									year={movie.year}
									title={movie.title}
									summary={movie.summary}
									poster={movie.medium_cover_image}
									genres={movie.genres}
								/>
							);
						})}
					</div>
				)}
			</section>
		);
	}
}

/* 페이지가 뜨면 먼저뜨는 함수(?)임 이걸 이용해서 setTimeout()을 통해 준비가 완료되면 
	componentDidMount() {
		setTimeout(() => {
			this.setState({ isLoading: false });
		}, 6000);
	} */
export default App;
